import React from 'react';
import Link from "gatsby-link";
import HobbitPreview from '../pages/images/hobbit_preview-500x420.jpg';
import BeorningPreview from '../pages/images/beorning_preview.jpg';
import HumanPreview from '../pages/images/human_preview.jpg';
import ElfPreview from '../pages/images/woodelf_preview.jpg';
import DwarfPreview from '../pages/images/dwarf_preview.jpg';
import UrukPreview from '../pages/images/urukhai_preview.jpg';
import HaradPreview from '../pages/images/haradrim_preview.jpg';
import LhuthPreview from '../pages/images/uruklhuth_preview.jpg';
import OlogPreview from '../pages/images/ologhai_preview.jpg';
import OrcPreview from '../pages/images/orc_preview.jpg';
import RacePreview from "../components/Races/RacePreview";


export default() =>
<div className="banner-race">
    <div className="jumbotron races-banner">
        <div className="container">
            <h1 className="text-center">Races</h1>
            <p>Whether as a stalwart defender of Vinyanost, or a fierce guardian of Dol-Guldur, deciding which race to play will define who you'll fight for in this neverending war.</p>
        </div>
    </div>
    <div className="container-fluid">
        <div className="row">
            <div className="col-lg-6">
                <div className="col-lg-12">
                    <h2 className="race-people">The Free People</h2>
                </div>
                <RacePreview title="Hobbits"
                 image={HobbitPreview}
                 path='/races/hobbits/'>
                Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.
                </RacePreview>
                <RacePreview title="Humans"
                 image={HumanPreview}
                 path='/races/humans/'
                >
                The race of Men is the second race of beings created by the supreme God, Ilúvatar, since they awoke at the start of the First Age, at the first rising of the Sun (FA 1), while the elves awoke three Ages before them. They are called the Afterborn (Quenya Atani, Sindarin: Edain) by the Elves.
                </RacePreview>
                <RacePreview title="Wood Elves"
                 image={ElfPreview}
                 path='/races/woodelves/'
                >
                The Silvan Elves of Mirkwood (formerly known as Greenwood the Great) were known to be distrustful of Dwarves but friendly to Men, with whom they trade. Mirkwood had been called Greenwood the Great until around the year TA 1050, when the shadow of the Dark Lord Sauron fell upon it, and men began to call it Mirkwood. During his journey to the Lonely Mountain, Bilbo Baggins had to rescue the Dwarves of Thorin II Oakenshield's company from these Elves. These Elves were also one of the armies in the Battle of the Five Armies.
                </RacePreview>
                <RacePreview title="Dwarves"
                 image={DwarfPreview}
                 path='/races/dwarves'
                >
                The Dwarves were made by Aulë, whom they themselves call Mahal, meaning “maker.” Aulë was unwilling to wait for the coming of the Children of Ilúvatar, for he was impatient and desired to have someone to teach his lore and crafts. Therefore, he made the first Seven Fathers of the Dwarves in secret in a hall under the mountains of Middle-earth. It was, however, not within Aulë's power to create life.
                </RacePreview>
                <RacePreview title="Beornings"
                 image={BeorningPreview}
                 path='/races/beornings/'
                >
                The origins of Beornings are not known, save that they dwelt in the Vales of Anduin between Mirkwood and the Great River in the late Third Age. Unlike other Men, they had the ability to change into bears. They enjoyed being a bear so much they forgot how to turn back into humans.
                </RacePreview>
            </div>
            <div className="col-lg-6">
                <div className="col-lg-12">
                    <h2 className="race-people">The Forces of the Shadow</h2>
                </div>
                <RacePreview title="Uruk-Hais"
                 image={UrukPreview}
                 path='/races/uruk-hais'
                >
                Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.
                </RacePreview>
                <RacePreview title="Common Orcs"
                 image={OrcPreview}
                 path='/races/orcs'
                >
                Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.
                </RacePreview>
                <RacePreview title="Olog-Hais"
                 image={OlogPreview}
                 path='/races/olog-hais'
                >
                Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.
                </RacePreview>
                <RacePreview title="Uruk-Lhuths"
                 image={LhuthPreview}
                 path='/races/uruk-lhuths'
                >
                Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.
                </RacePreview>
                <RacePreview title="Haradrims"
                 image={HaradPreview}
                 path='/races/haradrims'
                >
                Hobbits, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin.
                </RacePreview>
            </div>
        </div>
    </div>
</div>


